<template>
    <div class="gymInfo">
      <div class="header">
        <span @click="back" class="back">
          <img src="../assets/img/back.png">
        </span>
        <span>浦东太极拳馆</span>
        <span class="more" @click="toggleMore">
          <img src="../assets/img/more.png">
          <ul v-show="isShow" class="moreMenu" id="moreMenu">
            <li v-for="(action, index) in actions" :key="index">{{ action.link }}</li>
          </ul>
        </span>
      </div>
      <div class="mask" v-show="hasMask" @click="toggleMore"></div>
    </div>
</template>

<script>
export default {
  name: 'gymInfo',
  data () {
    return {
      isShow: false,
      hasMask: false,
      actions: [{
        link: '收藏'
      },
      {
        link: '分享'
      },
      {
        link: '举报'
      }]
    }
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    toggleMore () {
      this.isShow = !this.isShow
      this.hasMask = !this.hasMask
    }
  }
}
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
    font-size: 0.48rem;
  }

  .header {
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
    position: relative;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back {
    height: 0.5rem;
    width: 0.266667rem;
  }

  .more {
    height: 0.6rem;
    width: 0.13rem;
    position: relative;
  }

  img {
    width: 100%;
    height: 100%;
  }

 .moreMenu {
    position: absolute;
    list-style-type: none;
    background: #f5f5f5;
    top: 1.2rem;
    right: 0.5rem;
    border-radius: 0.2rem;
    z-index: 99;
    padding: 0 0.65rem;
    box-shadow: -1px 1px 3px #f5f5f5;
  }

  .moreMenu li {
    width: 1.466667rem;
    color: #ff8309;
    padding: 0.5rem 0;
    font-size: 0.4266667rem;
  }

  .moreMenu li:nth-child(2) {
    border-top: 1px solid #e6e6e6;
    border-bottom: 1px solid #e6e6e6;
  }
  .mask {
    z-index: 9999;
    position: absolute;
    top: -10rem;
    width: 100%;
    height: 2000%;
    background: #ffffff;
    opacity: 0;
  }
</style>



